.app {
    padding: 0 3%;

    a, a:visited {
        color: #181a1b;
    }

    @media (prefers-color-scheme: dark) {
        background: #181a1b;

        h1, h2, h3, h4, p, a, a:visited {
            color: #e8e6e3;
        }
    }
}

.menu-nav {
    display: flex;
    justify-content: space-evenly;
    padding-top: 5px;
}

.banner {
    position: relative;
}

.banner {
    height: auto;
}

.banner-img {
    opacity: 0.2;
    width: 100%;
    height: 100%;
    overflow: hidden;
    position: absolute;
    object-fit: none;
}

.banner-title {
    color: #181a1b;
    text-align: center;
    line-height: 10vh;
    position: relative;

    @media (prefers-color-scheme: dark) {
        color: #e8e6e3;
    }
}

.tierlist-page {
    
    .not-ranked { 
        display: none;
    }
    
    .tierlists {
        @media (min-width: 1024px) {
            display: flex;
            flex-wrap: wrap;
            justify-content: center;
            padding-bottom: 20px;
        
            .damage-dealer, .hybrid, .utility {
                margin-top: 40px;
                max-width: 750px;
                width: 50%;
            }
        }
    }

    .tierlist-row {
        display: flex;
      
        > * {
            width: 7%;
            border: 1px solid gray;
        }
      
        .tier-title {
            display: flex;
            background: #181a1b;
            color: #e8e6e3;
            text-transform: uppercase;
            justify-content: center;
            align-items: center;
            font-size: auto;
            width: 5%;

            @media (prefers-color-scheme: dark) {
                border: 1px solid #e8e6e3;
            }
        }
    }
      
    .not-included-row {
        justify-content: center;
        margin: 0;

        @media (min-width: 1024px) {
            margin: 0 30%;
        }

        @media(max-width: 1024px) {
            flex-wrap: wrap;
            
            .sync-pair-icon {
                width: 15%;
            }
        }
    }
      
    .submit-btn {
        display: flex;
        justify-content: center;

        button {
            border: 1px solid #181a1b;
            border-radius: 10px;
            font-size: 24px;
            margin: 20px;
            padding: 10px 20px;
            text-transform: uppercase;

            @media (prefers-color-scheme: dark) {
                border: 1px solid #e8e6e3;
                background: darkgray;
                color: #181a1b;
            }
        }
    }

    @media (prefers-color-scheme: dark) {
        label, h1, h2, h3, h4, p {
            color: #e8e6e3;
        }
    }
}


.header-row {
    text-align: center;
}

.sync-pair-icon {
    .sync-pair {
        margin: 5%;
        width: 90%;
    }

    .red {
        background: red;
    }
  
    .orange {
        background: orange;
    }
  
    .yellow {
        background: yellow;
    }
  
    .green {
        background: lightgreen;
    }
  
    .black {
        background: #181a1b;

        @media (prefers-color-scheme: dark) {
            background: #e8e6e3;
        }
    }

    :has(.not-found) {
        opacity: 0.2;
    }

    :has(.filter-excluded) {
        opacity: 1;
    }
}

.sync-pair-filters {
    @media (min-width: 1024px) {
        display: flex;

        > * {
            margin: 10px;
            width: calc(50% - 20px);
        }
    
        .type-trainer-filters, .misc-theme-filters {
            display: flex;
    
            > * {
                margin: 10px;
                width: calc(50% - 20px);
            }
    
            > div > h3 {
                text-align: center;
            }
        }
    }

    > div {
        border: 1px solid lightgray;
        border-radius: 15px;
        margin: 5px 0px;
        padding: 0px 5px 15px 5px;

        @media (prefers-color-scheme: dark) {
            border: 1px solid darkgray;
        }
    }
}

.type-option, .trainer-option, .role-option {
    display: flex;
    align-items: center;
}

.dropdown-img, .header-img {
    min-width: 45px;
    max-width: 45px;
    margin-right: 5px;
}

.role-acquisition-filters {
    .role-sections {
        display: flex;
        justify-content: space-evenly;
    }

    .acquisition-sections {
        display: flex;
        justify-content: space-around;
    }

    .filter-option {
        padding-right: 3px;
        font-size: 14px;
    }
}

.section-header {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

.homepage-resource {
    display: flex;
    border: 1px solid gray;

    .resource-overview {
        width: 65%;
        margin: 20px;
    }

    .resource-thumbnail {
        width: 35%;
        margin: 20px;
        padding-top: 20px;

        > img {
            max-width: 100%;
        }
    }
}